.parallax-container {
  position: relative;
  overflow: hidden;
}

.parallax-container::after {
  content: " ";
  position: absolute;
  background: linear-gradient(
    0deg,
    rgba(250, 150, 36, 1) 0%,
    rgba(0, 128, 249, 0) 100%
  );
  height: 8%;
  width: 100%;
  bottom: 0;
  left: 0;
}

.parallax-container #scene .layer img {
  width: 100%;
}

.trees {
  position: relative;
}

.trees::before {
  content: " ";
  width: 100%;
  height: 10%;
  background: url(../../images/designHome/3treesMidground.png);
  background-size: 100%;
  position: absolute;
  top: 0;
  transform: translateY(-30%);
  z-index: -1;
}

.trees::after {
  content: " ";
  position: absolute;
  background-color: rgb(116, 134, 51);
  height: 10%;
  width: 150%;
  bottom: -3%;
  left: -10%;
  z-index: -1;
}
